<template>
  <div
    v-if="api_data"
    id="responses"
    class="table-container box"
  >
    <table 
      class="table is-striped is-hoverable is-fullwidth" 
      style="border: 1px solid #dbdbdb;"
    >
      <caption class="mb-0 p-2">
        <span class="title">{{ api_data.title }}</span><br><span class="subtitle">Inscrições
        </span>
      </caption>
      <thead>
        <tr>
          <th
            style="text-align: center; vertical-align: middle;"
          >
            Participante
          </th>
          <th
            v-if="!event"
            style="text-align: center; vertical-align: middle;"
          >
            Evento
          </th>
          <th
            style="text-align: center; vertical-align: middle;"
          >
            Estado
          </th>
        </tr>
        <tr>
          <th class="p-0">
            <div class="field">
              <div class="control has-icons-left">
                <input
                  v-model="authorSearch"
                  type="text"
                  class="input is-info is-small m-0"
                  placeholder="Pesquisar por autor"
                >
                <span class="icon is-small is-left">
                  <i class="fas fa-search" />
                </span>
              </div>
            </div>
          </th>
          <th 
            v-if="!event" 
            class="p-0"
          >
            <div class="field">
              <div class="control has-icons-left">
                <input
                  v-model="eventSearch"
                  type="text"
                  class="input is-info is-small m-0"
                  placeholder="Pesquisar por evento"
                >
                <span class="icon is-small is-left">
                  <i class="fas fa-search" />
                </span>
              </div>
            </div>
          </th>
          <th class="p-0">
            <div class="field">
              <div class="control has-icons-left">
                <input
                  v-model="statusSearch"
                  type="text"
                  class="input is-info is-small m-0"
                  placeholder="Pesquisar por estado (Validada / Presente)"
                >
                <span class="icon is-small is-left">
                  <i class="fas fa-search" />
                </span>
              </div>
            </div>
          </th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="(response, responseIndex) in filteredResponses()"
          :key="responseIndex"
        >
          <td class="author">
            <span
              v-if="response.author"
              :href="'/profile/'+response.answerer"
            >{{ response.author }}</span>
            <template v-else>
              Anónimo
            </template><span
              :title="new Date(response.created_at).toLocaleString()"
            >, há {{ timeSince(response.created_at) }}</span><span
              v-if="response.created_at != response.updated_at"
              :class="response.created_at != response.updated_at ? 'edited' : ''"
              :title="'Editado por ' + (!response.editor ? 'Anónimo' : response.editor) + ' há ' + timeSince(response.updated_at) + ' (' + new Date(response.updated_at).toLocaleString() + ')'"
            >*</span><br>
            <a
              :href="'/forms/respond/' + response.form + '/?event=' + response.event + '&edit=' + response.id"
            >Editar Resposta</a>
          </td>
          <td v-if="!event">
            {{ response.eventName }}
          </td>
          <td
            style="vertical-align: middle;"
            align="center"
            v-if="statuses"
          >
            <span
              v-if="loading_status[response.id] === 'error'"
              class="is-danger connection-error"
            >
              Houve um erro de conexão. Por favor recarregue a página.
            </span>
            <div
              v-else
              class="flex column vca p-0"
            >
              <a
                class="mb-2 is-size-5 flex row vca"
                @click="modal_open[response.id] = true"
              >{{ statuses?.[answerStatuses[response.id]].name }} 
                <span
                  class="icon flex row vca ml-2"
                  style="margin-top: 2px;"
                ><i class="fas fa-eye" /></span>
              </a>
              <div
                class="modal"
                :class="{'is-active' : modal_open[response.id]}"
              >
                <div class="modal-background" />
                <div class="modal-card">
                  <header class="modal-card-head">
                    <p class="modal-card-title">
                      Inscrição de {{ response.author }}
                    </p>
                    <button
                      class="delete"
                      aria-label="close"
                      @click="modal_open[response.id] = undefined"
                    />
                  </header>
                  <section class="modal-card-body">  
                    <div
                      v-for="(section, sectionIndex) in response.content.sections"
                      :key="sectionIndex"
                      style="display: contents;"
                    >
                      <span class="title"><template v-if="!api_data.form.hide_section_numbers">
                        {{ sectionIndex+1 }}. </template>{{ api_data.form.sections[sectionIndex].section_name }}
                      </span>
                      <div
                        v-for="(answer, answerIndex) in section"
                        :key="answerIndex"
                        class="flex column"
                        :style="answer.answer === undefined ? 'background-color: rgba(0, 0, 0, 0.25);' : ''"
                      >
                        <hr class="mt-0">
                        <span class="subtitle is-bold mb-2">
                          <template v-if="!api_data.form.hide_section_numbers">
                            {{ sectionIndex+1 }}.</template>
                          <template v-if="!api_data.form.hide_question_numbers">
                            {{ answerIndex+1 }}.</template> {{ api_data.form.sections[sectionIndex].questions[answerIndex].question }}
                        </span>
                        <span>
                          <b>
                            Resposta: 
                          </b>
                          <template v-if="answer.answer !== undefined">
                            <template v-if="Object.prototype.toString.call( answer.answer ) === '[object Array]'">
                              <li
                                v-for="(option, optionIndex) in answer.answer"
                                :key="optionIndex"
                              >
                                {{ option }}
                              </li>
                            </template>
                            <template v-else>
                              {{ answer.answer }}
                            </template>
                          </template>
                          <template v-else>
                            Sem resposta
                          </template>
                        </span>
                      </div>
                    </div>
                  </section>
                  <footer class="modal-card-foot flex row ca">
                    <button
                      v-if="answerStatuses[response.id] == 1 || answerStatuses[response.id] == 2"
                      :class="{'is-loading' : loading_status[response.id] === 'loading'}"
                      type="button"
                      class="button is-danger is-light"
                      @click="answerStatuses[response.id] = 3; changeStatus(response.id, answerStatuses[response.id])"
                    >
                      Invalidar Inscrição
                    </button>
                    <button
                      v-if="answerStatuses[response.id] == 1 || answerStatuses[response.id] == 3"
                      :class="{'is-loading' : loading_status[response.id] === 'loading'}"
                      type="button"
                      class="button is-success"
                      @click="answerStatuses[response.id] = 2; changeStatus(response.id, answerStatuses[response.id])"
                    >
                      Validar Inscrição
                    </button>
                  </footer>
                </div>
              </div>
            </div>
          </td>
          <td v-else> A carregar... </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import * as components from "@/fields";
import { API } from "@/http-common";
import { decodeSections, getCookie, removeExpositionSections, timeSince, stringIn } from "@/common";
export default {
  name: "ViewAllResponses",
  components,
  data() {
    return {
      api_data: undefined,
      form_responses: [],
      displayed_sections: [],
      event: undefined,
      user: undefined,
      statuses: undefined,
      authorSearch: "",
      eventSearch: "",
      statusSearch: "",
      answerStatuses: [],
      interval : undefined,
      loading_status : {},
      modal_open : {}
    };
  },
  created() {
    const form = API.get("form/".concat(this.$route.params.id))
      .then((response) => 
        this.initializeData(response)
      )
      .catch((error) => console.log(error.response));
    var queryString = "formAnswer/?form=".concat(this.$route.params.id);
    if (this.$route.query.event !== undefined) {
      queryString = queryString.concat("&event=").concat(this.$route.query.event)
      API.get("event/".concat(this.$route.query.event))
       .then(response => this.event = response.data.name)
       .catch((error) => console.log(error.response));
    }
    // if (this.$route.query.user !== undefined) {
    //   queryString = queryString.concat("&user=").concat(this.$route.query.user)
    //   API.get("user/".concat(this.$route.query.user))
    //    .then(response => this.user = response.data.username);
    // }
    API.get(queryString)
      .then(async (response) => {
          await form;
          this.initializeAnswers(response)
        }
      );

    API.get("answerStatus/")
      .then((response) => {
        this.statuses = response.data;
      });
    this.getStatuses();
    //this.interval = setInterval(() => this.getStatuses(), 10000);
  },
  methods: {
    getStatuses() {
      API.get("formAnswerStatus/")
      .then((response) => {
        this.answerStatuses = response.data;
      });
    },
    changeStatus(answer, status) {
      this.loading_status[answer] = 'loading';
      setTimeout(this.patchStatus.bind(this, answer, status), 0);
    },
    patchStatus(answer, status) {
      const options = {
        method: "patch",
        url: "/formAnswerStatus/" + answer + "/",
        headers: {
          "X-CSRFToken": getCookie("csrftoken"),
        },
        data: {
          "status": status
        },
        transformResponse: [
          (data) => {
            this.response = data;
            return data;
          },
        ],
      };
      API(options)
        .then(() => this.loading_status[answer] = undefined)
        .catch((error) => {
          console.log(error); 
          this.loading_status[answer] = 'error';
          this.answerStatuses[answer] = 'error';
        });
    },
    initializeData(response) {
      this.api_data = removeExpositionSections(response.data);
      for (var i = 0; i < this.api_data.form.sections.length; /*do nothing*/) {
        if (this.api_data.form.sections[i].questions.length > 0)
          this.displayed_sections[i++] = true;
        else
          this.api_data.form.sections.splice(i, 1);
      }
    },
    initializeAnswers(response) {
      this.form_responses = decodeSections(response.data, this.api_data);
    },
    allFalse() {
      for (var i = 0; i < this.displayed_sections.length; i++)
        if (this.displayed_sections[i] == true)
          return false;
      return true;
    },
    filteredResponses() {
      var result = this.form_responses;
      if (this.authorSearch != "")
        result = result.filter(x => stringIn(x.author === undefined ? "Anónimo" : x.author, this.authorSearch));
      if (this.eventSearch != "")
        result = result.filter(x => {return x.eventName ? stringIn(x.eventName, this.eventSearch) : false});
      if (this.statusSearch != "")
        result = result.filter(x => stringIn(this.statuses[this.answerStatuses[x.id]].name, this.statusSearch));
      for (var sectionIndex = 0; sectionIndex < this.api_data.form.sections.length; sectionIndex++) {
        if (this.displayed_sections[sectionIndex]) {
          var section = this.api_data.form.sections[sectionIndex];
          for (var questionIndex = 0; questionIndex < section.questions.length; questionIndex++) {
            var question = section.questions[questionIndex]
            if (question.filter && question.filter != "")
              result = result.filter(x => stringIn(String(x.content.sections[sectionIndex][questionIndex].answer), question.filter));
          }
        }
      }
      return result;
    },
    timeSince: timeSince
  }
};
</script>

<style scoped lang="scss">
  .section-first {
    border-left: 1px solid #dbdbdb !important;
  }
  .section-last {
    border-right: 1px solid #dbdbdb !important;
  }
  .mt-1px {
    margin-top: 1px;
  }

  .connection-error>* {
    background-color: #f14668 !important;
    color: white !important;
  }

  .connection-error option {
    background-color: white !important;
    color: black !important;
  }
</style>
